<template>
  <footer id="appFooter">
    <nav id="appNav">
      <ul class="gnb">
        <li>
          <router-link to="/headline"></router-link>
        </li>
        <li>
          <router-link to="/categorylist"></router-link>
        </li>
        <li>
          <router-link to="/posts"></router-link>
        </li>
        <li @click="activeSearch"></li>
        <li v-if="this.$store.state.accessToken === null">        
          <router-link to="/login"></router-link>          
        </li>
        <li v-else>
          <router-link to="/mypage"></router-link>
        </li>        
      </ul>
    </nav>

  </footer>
</template>
<script>
export default {
  data() {
    return {
      searchState: false,
      chkLogin: localStorage.accessToken,
      loading: false
    };
  },
  mounted() {
    localStorage.accessToken !== "null"
      ? (this.$store.state.accessToken = localStorage.accessToken)
      : null;
  },
  updated() {},
  methods: {
    activeSearch: function() {
      const searchForm = document.querySelector(".search_form");
      this.searchState = !this.searchState;
      this.searchState
        ? searchForm.classList.add("on")
        : searchForm.classList.remove("on");
    }
  }
};
</script>



